:root {
  --background1: #00000fff;
  --text1:#FFF;
  --background2: #FFF;
  --text2:#00000fff;
}

:root {
  cursor: url("../../../images/cursor/gold.png"), auto;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

html {
  font-size: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  display: flex;
  width: 100%;
  align-items: center;
}

:root {
  --sectionHeigth: 100vh;
}

h1,
h2,
h3 {
  font-weight: 700;
  line-height: 1.1;
  margin-top: 0;
}

p {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.introduction {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: var(--background1);
  color: var(--text1);
  height: var(--sectionHeigth);
}

p {
  justify-content: center;
}

.resume {
  height: var(--sectionHeight);
  background: var(--background1);
  color: var(--text1);
}

.textSphere {
  display: flex;
  justify-content: center;
  align-items: center;
}

.certs {
  display: flex;
  justify-content: center;
}

.certs img {
  width: 150px;
}

.about {
  display: block;
  padding: 20px;
  background: var(--background2);
  color: var(--text2);
  height: var(--sectionHeigth);
}

.projects {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: var(--background1);
  color: var(--text1);
  height: var(--sectionHeigth);
}

.contact {
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--background2);
  color: var(--text2);
  height: var(--sectionHeigth);
}

#qrcode {
  width: 200px;
  height: 200px;
}